.metrics-crossing-indicator .crossing-indicator-items{list-style:none;padding:0;font-size:0.8rem}.metrics-crossing-indicator .crossing-indicator-items .crossing-indicator-item{border:1px solid black;padding:calc(var(--gutter-small) / 2)}.metrics-crossing-indicator .crossing-indicator-items .crossing-indicator-item.active{background:black;color:white}.metrics-crossing-indicator .crossing-indicator-items .crossing-indicator-item.active .info-tip{border-color:white}

