t li .checkmark:after{content:"";position:absolute;display:none}.filter-form .variable-inputs .privacy-list-container .privacy-list li input:checked ~ .checkmark:after{display:block}.filter-form .variable-inputs .privacy-list-container .privacy-list li .checkmark:after{left:0.4em;top:0.1em;width:0.3em;height:0.7em;border:solid black;border-width:0 3px 3px 0;transform:rotate(45deg)}

.brushable-scatterplot-container{position:relative}.brushable-scatterplot-container .brushable-scatterplot{background:white;cursor:crosshair}.brushable-scatterplot-container .brushable-scatterplot.is-readonly{cursor:default}.brushable-scatterplot-container .brushable-scatterplot .brush{fill:black;opacity:0.3}.brushable-scatterplot-container .brushable-scatterplot text{font-family:var(--font-family-main);-webkit-user-select:none;-ms-user-select:none;user-select:none}.brushable-scatterplot-container .brushable-scatterplot .dot{fill:darkolivegreen;opacity:0.3;pointer-events:all;transition:0.2s ease all}.brushable-scatterplot-container .brushable-scatterplot .dot.is-active{opacity:0.8}.brushable-scatterplot-container .brushable-scatterplot .dot.is-highlighted{r:10px;fill:brown}.brushable-scatterplot-container .brushable-scatterplot.is-brushing .dot{pointer-events:none}.brushable-scatterplot-container .brushable-scatterplot:not(.is-readonly):not(.is-minified) .dot:hover{z-index:5;r:10px}.brushable-scatterplot-container .chart-tooltip{position:absolute;background:black;color:white;transition:0.2s ease all;pointer-events:none;font-size:0.8rem;padding:var(--gutter-small);transform:scale(0.1);transform-origin:top left;opacity:0}.brushable-scatterplot-container .chart-tooltip.is-active{transform:scale(1);opacity:1}.brushable-scatterplot-container .chart-tooltip ul{list-style:none;padding:0;margin:0}.brushable-scatterplot-container .chart-tooltip ul .tooltip-info.active{font-weight:800}

